<template>
    <div class="login-wrap">
        <div class="nav_container">
          <div class="nav-centre">
            <img src="http://res.yujingceping.com/res/resources/front/imgs/Logo.png">
          </div>
        </div>
        <!--<div class="ms-title">教务管理系统</div>-->
        <div class="loginBody">
          <div class="loginElement">
            <div class="ms-login">
              <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="60px" class="demo-ruleForm">
                <el-form-item prop="username" label="用户名:">
                  <el-input :maxlength="19" @change="queryMsgAllow(ruleForm)" id="userName" v-model="ruleForm.username" placeholder="username"></el-input>
                </el-form-item>
                <el-form-item prop="password" label="密  码:">
                  <el-input :maxlength="19" @change="queryMsgAllow(ruleForm)" id="passWord" type="password" placeholder="password" v-model="ruleForm.password" @keyup.enter.native="submitForm('ruleForm')"></el-input>
                </el-form-item>
                <el-form-item prop="captcha" label="验证码:">
                  <el-input id="captcha" @change="queryMsgAllow(ruleForm)" v-model="ruleForm.captcha" placeholder="验证码" @keyup.enter.native="submitForm('ruleForm')"></el-input>
                  <div class="captchaWrap">
                    <img @click="reloadImg" :src="captchaImg" alt="验证码">
                  </div>
                  <div @click="reloadImg" class="captchaText">看不清？点击换一张</div>

                </el-form-item>
                <div class="login-btn">
                  <el-button :class="btnStatus ? 'primary' : 'notAllowBtn' " type="primary"  @click="submitForm('ruleForm')">登录</el-button>
                </div>
                <p style="font-size:12px;line-height:30px;color:#999;">Tips : 用户名和密码随便填。</p>
                <router-link to="/forgetPassword"><span class="forgetPassword">忘记密码</span></router-link>
              </el-form>
            </div>
            <div class="loginbg_left"></div>
            <div class="loginbg_right"></div>
          </div>
        </div>
    </div>
</template>

<script>
    export default {
        data: function(){
          var validateUser = (rule, value, callback) => {
            if (value === '') {
              callback(new Error('请输入用户名'));
            } else {
              var patrn=/^(\w){1,19}$/;
              if (!patrn.exec(value)) {
//                  this.$refs.ruleForm2.validateField('checkPass');
                callback(new Error('用户名不包含汉字或空格及特殊符号'));
              }else{
                callback();
              }
            }
          };
          var validatePass = (rule, value, callback) => {
            if (value === '') {
              callback(new Error('请输入密码'));
            } else {
              var patrn=/^(\w){6,19}$/;
              if (!patrn.exec(value)) {
//                  this.$refs.ruleForm2.validateField('checkPass');
                callback(new Error('至少6位不包含空格及特殊符号的密码'));
              }else{
                callback();
              }
            }
          };
          var validateCaptcha = (rule, value, callback) => {
            if (value === '') {
              callback(new Error('请输入验证码'));
            } else {
              var patrn=/^[A-Za-z0-9]+$/;
              if (!patrn.exec(value)) {
//                  this.$refs.ruleForm2.validateField('checkPass');
                callback(new Error('数字字母的组合'));
              }else{
                callback();
              }
            }
          };
            return {
                ruleForm: {
                    username: '',
                    password: '',
                    captcha: ''
                },
                rules: {
                    username: [
                        { validator:validateUser, trigger: 'blur' }
                    ],
                    password: [
                        { validator:validatePass, trigger: 'blur' }
                    ],
                    captcha: [
                      { validator:validateCaptcha, trigger: 'blur' }
                    ]
                },
                captchaImg:"http://60.205.115.173:8888/yitai-educational/captcha?ts="+new Date().getTime(),
                btnStatus : false
            }
        },
        mounted: function () {
//            let $this = this;
//            this.$ajax('/captcha',
//  //          $this.param
//            ).then(function (response) {
////              console.log(response);
//                $this.captchaImg = response;
//            }).catch(function (error) {
//                console.log(error);
//            });
        },
        methods: {
            submitForm(formName) {

              const self = this;
              self.$refs[formName].validate((valid) => {
                if (valid) {
                  self.$ajax('login',
                    self.ruleForm
                    ).then(function (response) {
                        console.log(response);
                        if (response.code == 200){
//                          localStorage.setItem('ms_username',self.ruleForm.username);
                          self.$router.push('/loginCheck');
                        }else {
                          self.$alert(response.message, '提示', {
                          confirmButtonText: '确定',
                          callback: action => {}
                        });
                        }
                      }).catch(function (error) {
                          console.log(error);
                      });
//
                } else {
                  console.log('error submit!!');
                  return false;
                }
              });

            },
            reloadImg(){
              this.captchaImg = "http://60.205.115.173:8888/yitai-educational/captcha?ts="+new Date().getTime()
            },
            queryMsgAllow(val){
            if (val.username.length>0 && val.password.length>0 && val.captcha.length>0){
                this.btnStatus = true;
            }else {
                this.btnStatus = false;
            }
          }
        }
    }
</script>

<style scoped>
    .login-wrap{
      margin: 0 auto;
    }
    .nav_container {
      width: 100%;
      height: auto;
      position: relative;
      z-index: 100;
      background: #fff;
    }
    .nav-centre {
      width: 1024px;
      height: 80px;
      background: #fff;
      margin: 0 auto;
      overflow: hidden;
    }
    .nav_container img {
      width: 150px;
      height: 38px;
      margin-top: 21px;
      float: left;
      margin-right: 57px;
    }
    .loginBody {
      background: url(http://res.yujingceping.com/res/resources/front/imgs/body_containerbg.jpg) repeat-x;
      width: 100%;
      height: 559px;
      /*margin-top: 100px;*/
      /*position: relative;*/
      /*top: 50px;*/
    }
    .loginElement {
      position: relative;
      margin: 0 auto;
      width: 1024px;
      height: 559px;
      background: url(http://res.yujingceping.com/res/resources/front/imgs/login_bg.jpg);
    }
    .loginbg_left {
      position: absolute;
      top: 0;
      left: -174px;
      width: 174px;
      height: 559px;
      background: url(http://res.yujingceping.com/res/resources/front/imgs/loginbg_left.jpg);
    }
    .loginbg_right {
      display: block;
      position: absolute;
      top: 0;
      left: 1008px;
      width: 174px;
      height: 559px;
      background: url(http://res.yujingceping.com/res/resources/front/imgs/loginbg_right.jpg);
    }
    .ms-title{
        position: absolute;
        top:30%;
        width:100%;
        margin-top: -230px;
        text-align: center;
        font-size:30px;
        color: black;

    }
    .ms-login{
        position: absolute;
        right:0%;
        top:50%;
        width:280px;
        height:230px;
        margin-top:-170px;
        padding:40px 30px 40px 14px;
        border-radius: 5px;
        background: #fff;
    }
    .login-btn{
        margin-top: 40px;
        text-align: center;
    }
    .login-btn button{
        width:100%;
        height:36px;
    }
    .el-form-item__content {
      text-align: left;
    }
    #captcha {
      width: 100px;
      margin-left: 0px;
    }
    #captcha input{
      display: inline-block;
      width: 100px;
    }
    .captchaWrap {
      vertical-align: top;
      margin-left: 15px;
      display: inline-block;
      width: 90px;
      height: 36px;
    }
    .captchaWrap img {
      cursor: pointer;
      display: inline-block;
      width: 100%;
      height: 100%;
  }
    .el-form-item__content .captchaText{
      position: absolute;
      height: 0;
      right: 0;
      top: 35px;
      font-size: 12px;
      cursor: pointer;
      color: mediumblue;
      text-align: right;
      display: inline-block;
      text-decoration:underline;
      -moz-user-select:none;
      -webkit-user-select:none;
      user-select:none;
    }
    .captchaText:active {
      color: blue;
      -moz-user-select:none;
      -webkit-user-select:none;
      user-select:none;
    }
    .login-btn .notAllowBtn {
      background: gray;
      width:100%;
      height:36px;
      border: 1px solid gray;
    }
    .notAllowBtn:hover {
      background: gray;
      width:100%;
      height:36px;
    }
    .login-btn .notAllowBtn:active {
      background: gray;
      width:100%;
      height:36px;
    }
  .forgetPassword {
    position: absolute;
    right: 30px;
    bottom: 18px;
    font-size: 12px;
    cursor: pointer;
    color: grey;
    text-align: right;
    display: inline-block;
  }
    .forgetPassword:hover {
      color: mediumblue;
      text-decoration:underline;
    }
</style>



